"use client";

import { useState, useEffect } from 'react';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { ClippingCard } from '@/components/ClippingCard';
import { NewsletterScheduler } from '@/components/NewsletterScheduler';
import { 
  getPrintClippings, 
  getElectronicClippings, 
  getDigitalClippings,
  getAllClippings 
} from '@/lib/clippingService';
import { Clipping } from '@/lib/clippingTypes';
import ErrorBoundary from '@/components/ErrorBoundary';

export default function ClippingDashboard() {
  const [printClippings, setPrintClippings] = useState<Clipping[]>([]);
  const [electronicClippings, setElectronicClippings] = useState<Clipping[]>([]);
  const [digitalClippings, setDigitalClippings] = useState<Clipping[]>([]);
  const [allClippings, setAllClippings] = useState<Clipping[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const loadClippings = async () => {
      try {
        setLoading(true);
        setError(null);

        // Load all clipping types
        const print = getPrintClippings();
        const electronic = getElectronicClippings();
        const digital = getDigitalClippings();
        const all = getAllClippings();

        setPrintClippings(print);
        setElectronicClippings(electronic);
        setDigitalClippings(digital);
        setAllClippings(all);
      } catch (err) {
        console.error('Error loading clippings:', err);
        setError('Erro ao carregar os clippings. Tente novamente mais tarde.');
      } finally {
        setLoading(false);
      }
    };

    loadClippings();
  }, []);

  const renderClippingList = (clippings: Clipping[], emptyMessage: string) => {
    if (loading) {
      return (
        <div className="space-y-4">
          {[1, 2, 3].map((i) => (
            <Card key={i} className="animate-pulse">
              <CardHeader>
                <div className="h-4 bg-muted rounded w-3/4"></div>
                <div className="h-3 bg-muted rounded w-1/2"></div>
              </CardHeader>
              <CardContent>
                <div className="space-y-2">
                  <div className="h-3 bg-muted rounded"></div>
                  <div className="h-3 bg-muted rounded w-5/6"></div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      );
    }

    if (clippings.length === 0) {
      return (
        <Card>
          <CardContent className="flex items-center justify-center py-8">
            <p className="text-muted-foreground">{emptyMessage}</p>
          </CardContent>
        </Card>
      );
    }

    return (
      <div className="space-y-4">
        {clippings.map((clipping) => (
          <ClippingCard key={clipping.id} clipping={clipping} />
        ))}
      </div>
    );
  };

  if (error) {
    return (
      <div className="container mx-auto py-8">
        <Alert variant="destructive">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      </div>
    );
  }

  return (
    <ErrorBoundary>
      <div className="container mx-auto py-8 space-y-8">
      {/* Header */}
      <div className="space-y-2">
        <h1 className="text-3xl font-bold tracking-tight">
          Dashboard de Clipping - Prefeitura de S√£o Leopoldo
        </h1>
        <p className="text-muted-foreground">
          Monitoramento de mat√©rias jornal√≠sticas sobre a Prefeitura de S√£o Leopoldo
        </p>
      </div>

      {/* Statistics Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total de Clippings</CardTitle>
            <span className="text-2xl">üì∞</span>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{allClippings.length}</div>
            <p className="text-xs text-muted-foreground">
              Todas as m√≠dias
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">M√≠dia Impressa</CardTitle>
            <span className="text-2xl">üìÑ</span>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{printClippings.length}</div>
            <p className="text-xs text-muted-foreground">
              Jornais e revistas
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">M√≠dia Eletr√¥nica</CardTitle>
            <span className="text-2xl">üìª</span>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{electronicClippings.length}</div>
            <p className="text-xs text-muted-foreground">
              R√°dio e TV
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">M√≠dia Digital</CardTitle>
            <span className="text-2xl">üíª</span>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{digitalClippings.length}</div>
            <p className="text-xs text-muted-foreground">
              Sites e blogs
            </p>
          </CardContent>
        </Card>
      </div>

      {/* Main Content Tabs */}
      <Tabs defaultValue="all" className="space-y-4">
        <TabsList className="grid w-full grid-cols-5">
          <TabsTrigger value="all">Todos</TabsTrigger>
          <TabsTrigger value="print">Impressa</TabsTrigger>
          <TabsTrigger value="electronic">Eletr√¥nica</TabsTrigger>
          <TabsTrigger value="digital">Digital</TabsTrigger>
          <TabsTrigger value="newsletter">Newsletter</TabsTrigger>
        </TabsList>

        <TabsContent value="all" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>Todos os Clippings</CardTitle>
              <CardDescription>
                Visualiza√ß√£o consolidada de todas as m√≠dias monitoradas
              </CardDescription>
            </CardHeader>
            <CardContent>
              {renderClippingList(allClippings, "Nenhum clipping encontrado.")}
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="print" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>Clipping de M√≠dia Impressa</CardTitle>
              <CardDescription>
                Jornais e revistas - Enviado diariamente at√© √†s 08h00
              </CardDescription>
            </CardHeader>
            <CardContent>
              {renderClippingList(
                printClippings, 
                "Nenhum clipping de m√≠dia impressa encontrado."
              )}
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="electronic" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>Clipping de M√≠dia Eletr√¥nica</CardTitle>
              <CardDescription>
                R√°dio e TV - Monitoramento em tempo real (at√© 30 min ap√≥s veicula√ß√£o)
              </CardDescription>
            </CardHeader>
            <CardContent>
              {renderClippingList(
                electronicClippings, 
                "Nenhum clipping de m√≠dia eletr√¥nica encontrado."
              )}
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="digital" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>Clipping de M√≠dia Digital</CardTitle>
              <CardDescription>
                Sites, portais e blogs - Monitoramento em tempo real (at√© 1h ap√≥s publica√ß√£o)
              </CardDescription>
            </CardHeader>
            <CardContent>
              {renderClippingList(
                digitalClippings, 
                "Nenhum clipping de m√≠dia digital encontrado."
              )}
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="newsletter" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>Agendamento de Newsletter</CardTitle>
              <CardDescription>
                Envie newsletters por email e push notifications com clippings selecionados
              </CardDescription>
            </CardHeader>
            <CardContent>
              <NewsletterScheduler clippings={allClippings} />
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
      </div>
    </ErrorBoundary>
  );
}
