"use client";

import { useState, useEffect } from 'react';
import { Clipping, PrintClipping, ElectronicClipping, DigitalClipping } from '@/lib/clippingTypes';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { formatDistanceToNow } from 'date-fns';
import { ptBR } from 'date-fns/locale';

interface ClippingCardProps {
  clipping: Clipping;
}

export function ClippingCard({ clipping }: ClippingCardProps) {
  const [timeAgo, setTimeAgo] = useState<string>('');
  const [isClient, setIsClient] = useState(false);

  useEffect(() => {
    setIsClient(true);
    setTimeAgo(formatDistanceToNow(clipping.insertedAt, { addSuffix: true, locale: ptBR }));
  }, [clipping.insertedAt]);

  const formatDate = (date: Date) => {
    return new Intl.DateTimeFormat('pt-BR', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    }).format(date);
  };

  const renderPrintClipping = (clipping: PrintClipping) => (
    <>
      <div className="flex items-center gap-2 mb-2">
        <Badge variant="secondary">Impresso</Badge>
        <span className="text-sm text-muted-foreground">P√°gina {clipping.page}</span>
        <span className="text-sm text-muted-foreground">‚Ä¢</span>
        <span className="text-sm text-muted-foreground">{clipping.editoria}</span>
      </div>
      
      <div className="space-y-3">
        <p className="text-sm text-muted-foreground">
          Publicado em: {formatDate(clipping.publicationDate)}
        </p>
        
        <div className="prose prose-sm max-w-none">
          <div dangerouslySetInnerHTML={{ __html: clipping.htmlContent }} />
        </div>

        <div className="flex items-center gap-2">
          <img 
            src="https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/565d944f-bfeb-4cac-b0cf-07fe4f7b5496.png" 
            alt="Thumbnail preview of the printed media clipping showing newspaper page layout" 
            className="rounded border"
            onError={(e) => { 
              e.currentTarget.style.display = 'none';
            }} 
          />
          <Button variant="outline" size="sm" asChild>
            <a href={clipping.pdfLink} target="_blank" rel="noopener noreferrer">
              Ver PDF Completo
            </a>
          </Button>
        </div>
      </div>
    </>
  );

  const renderElectronicClipping = (clipping: ElectronicClipping) => (
    <>
      <div className="flex items-center gap-2 mb-2">
        <Badge variant={clipping.mediaType === 'radio' ? 'default' : 'destructive'}>
          {clipping.mediaType === 'radio' ? 'R√°dio' : 'TV'}
        </Badge>
        {clipping.duration && (
          <>
            <span className="text-sm text-muted-foreground">‚Ä¢</span>
            <span className="text-sm text-muted-foreground">{clipping.duration}</span>
          </>
        )}
      </div>
      
      <div className="space-y-3">
        <p className="text-sm text-muted-foreground">
          Veiculado em: {formatDate(clipping.broadcastTime)}
        </p>
        
        <div className="flex items-center gap-3 p-3 bg-muted rounded-lg">
          <div className="w-12 h-12 bg-primary/10 rounded-full flex items-center justify-center">
            {clipping.mediaType === 'radio' ? 'üéµ' : 'üì∫'}
          </div>
          <div className="flex-1">
            <p className="text-sm font-medium">Player de {clipping.mediaType === 'radio' ? '√Åudio' : 'V√≠deo'}</p>
            <p className="text-xs text-muted-foreground">
              Formato: {clipping.mediaType === 'radio' ? 'MP3' : 'MP4'}
            </p>
          </div>
          <Button variant="outline" size="sm" asChild>
            <a href={clipping.playerLink} target="_blank" rel="noopener noreferrer">
              Reproduzir
            </a>
          </Button>
        </div>
      </div>
    </>
  );

  const renderDigitalClipping = (clipping: DigitalClipping) => (
    <>
      <div className="flex items-center gap-2 mb-2">
        <Badge variant="outline">Digital</Badge>
        {clipping.author && (
          <>
            <span className="text-sm text-muted-foreground">‚Ä¢</span>
            <span className="text-sm text-muted-foreground">Por {clipping.author}</span>
          </>
        )}
      </div>
      
      <div className="space-y-3">
        <p className="text-sm text-muted-foreground">
          Publicado em: {formatDate(clipping.publicationTime)}
        </p>
        
        <p className="text-sm leading-relaxed">{clipping.content}</p>
        
        <div className="flex items-center gap-2">
          <img 
            src="https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/c1a099d8-9790-4db3-8744-e254186a4e2e.png" 
            alt="Thumbnail preview of the digital media clipping showing website screenshot" 
            className="rounded border"
            onError={(e) => { 
              e.currentTarget.style.display = 'none';
            }} 
          />
          <div className="flex flex-col gap-1">
            <Button variant="outline" size="sm" asChild>
              <a href={clipping.screenshotPdfLink} target="_blank" rel="noopener noreferrer">
                Ver Screenshot
              </a>
            </Button>
            <Button variant="outline" size="sm" asChild>
              <a href={clipping.originalSiteLink} target="_blank" rel="noopener noreferrer">
                Site Original
              </a>
            </Button>
          </div>
        </div>
      </div>
    </>
  );

  const renderClippingContent = () => {
    switch (clipping.type) {
      case 'print':
        return renderPrintClipping(clipping as PrintClipping);
      case 'electronic':
        return renderElectronicClipping(clipping as ElectronicClipping);
      case 'digital':
        return renderDigitalClipping(clipping as DigitalClipping);
      default:
        return <p>Tipo de clipping n√£o reconhecido</p>;
    }
  };

  return (
    <Card className="w-full">
      <CardHeader>
        <div className="flex items-start justify-between">
          <div className="flex-1">
            <CardTitle className="text-lg leading-tight mb-1">
              {clipping.headline}
            </CardTitle>
            <CardDescription className="flex items-center gap-2">
              <span className="font-medium">{clipping.source}</span>
              <span>‚Ä¢</span>
              <span suppressHydrationWarning>
                {isClient ? timeAgo : 'Carregando...'}
              </span>
            </CardDescription>
          </div>
        </div>
      </CardHeader>
      
      <CardContent>
        {renderClippingContent()}
      </CardContent>
    </Card>
  );
}
