"use client";

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Checkbox } from '@/components/ui/checkbox';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import { Clipping } from '@/lib/clippingTypes';

interface NewsletterSchedulerProps {
  clippings: Clipping[];
}

export function NewsletterScheduler({ clippings }: NewsletterSchedulerProps) {
  const [selectedClippings, setSelectedClippings] = useState<string[]>([]);
  const [recipients, setRecipients] = useState<string>('');
  const [subject, setSubject] = useState('');
  const [customMessage, setCustomMessage] = useState('');
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);

  const handleClippingToggle = (clippingId: string) => {
    setSelectedClippings(prev => 
      prev.includes(clippingId)
        ? prev.filter(id => id !== clippingId)
        : [...prev, clippingId]
    );
  };

  const handleSelectAll = () => {
    if (selectedClippings.length === clippings.length) {
      setSelectedClippings([]);
    } else {
      setSelectedClippings(clippings.map(c => c.id));
    }
  };

  const validateForm = () => {
    if (selectedClippings.length === 0) {
      setError('Selecione pelo menos um clipping para enviar.');
      return false;
    }

    if (!recipients.trim()) {
      setError('Informe pelo menos um destinat√°rio.');
      return false;
    }

    // Validate email format
    const emailList = recipients.split(',').map(email => email.trim());
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    const invalidEmails = emailList.filter(email => !emailRegex.test(email));
    
    if (invalidEmails.length > 0) {
      setError(`Emails inv√°lidos: ${invalidEmails.join(', ')}`);
      return false;
    }

    return true;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setLoading(true);
    setError(null);
    setSuccess(null);

    try {
      const response = await fetch('/api/newsletter', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          selectedClippings,
          recipients: recipients.split(',').map(email => email.trim()),
          subject: subject || 'Clipping Di√°rio - Prefeitura de S√£o Leopoldo',
          customMessage
        }),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.message || 'Erro ao enviar newsletter');
      }

      setSuccess(`Newsletter enviada com sucesso para ${data.sentCount} destinat√°rios!`);
      
      // Reset form
      setSelectedClippings([]);
      setRecipients('');
      setSubject('');
      setCustomMessage('');
      
    } catch (err) {
      console.error('Error sending newsletter:', err);
      setError(err instanceof Error ? err.message : 'Erro desconhecido ao enviar newsletter');
    } finally {
      setLoading(false);
    }
  };

  const getClippingTypeLabel = (type: string) => {
    switch (type) {
      case 'print': return 'Impressa';
      case 'electronic': return 'Eletr√¥nica';
      case 'digital': return 'Digital';
      default: return type;
    }
  };

  const getClippingTypeBadgeVariant = (type: string) => {
    switch (type) {
      case 'print': return 'secondary' as const;
      case 'electronic': return 'default' as const;
      case 'digital': return 'outline' as const;
      default: return 'secondary' as const;
    }
  };

  return (
    <div className="space-y-6">
      {/* Success/Error Messages */}
      {success && (
        <Alert>
          <AlertDescription>{success}</AlertDescription>
        </Alert>
      )}
      
      {error && (
        <Alert variant="destructive">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      <form onSubmit={handleSubmit} className="space-y-6">
        {/* Recipients */}
        <div className="space-y-2">
          <Label htmlFor="recipients">Destinat√°rios *</Label>
          <Input
            id="recipients"
            type="text"
            placeholder="email1@exemplo.com, email2@exemplo.com"
            value={recipients}
            onChange={(e) => setRecipients(e.target.value)}
            required
          />
          <p className="text-sm text-muted-foreground">
            Separe m√∫ltiplos emails com v√≠rgula
          </p>
        </div>

        {/* Subject */}
        <div className="space-y-2">
          <Label htmlFor="subject">Assunto</Label>
          <Input
            id="subject"
            type="text"
            placeholder="Clipping Di√°rio - Prefeitura de S√£o Leopoldo"
            value={subject}
            onChange={(e) => setSubject(e.target.value)}
          />
        </div>

        {/* Custom Message */}
        <div className="space-y-2">
          <Label htmlFor="customMessage">Mensagem Personalizada</Label>
          <Textarea
            id="customMessage"
            placeholder="Adicione uma mensagem personalizada que ser√° inclu√≠da no in√≠cio da newsletter..."
            value={customMessage}
            onChange={(e) => setCustomMessage(e.target.value)}
            rows={3}
          />
        </div>

        <Separator />

        {/* Clipping Selection */}
        <div className="space-y-4">
          <div className="flex items-center justify-between">
            <Label className="text-base font-medium">
              Selecionar Clippings * ({selectedClippings.length} de {clippings.length} selecionados)
            </Label>
            <Button
              type="button"
              variant="outline"
              size="sm"
              onClick={handleSelectAll}
            >
              {selectedClippings.length === clippings.length ? 'Desmarcar Todos' : 'Selecionar Todos'}
            </Button>
          </div>

          <div className="max-h-96 overflow-y-auto space-y-3 border rounded-lg p-4">
            {clippings.length === 0 ? (
              <p className="text-muted-foreground text-center py-4">
                Nenhum clipping dispon√≠vel para sele√ß√£o.
              </p>
            ) : (
              clippings.map((clipping) => (
                <Card key={clipping.id} className="relative">
                  <CardHeader className="pb-3">
                    <div className="flex items-start space-x-3">
                      <Checkbox
                        id={clipping.id}
                        checked={selectedClippings.includes(clipping.id)}
                        onCheckedChange={() => handleClippingToggle(clipping.id)}
                        className="mt-1"
                      />
                      <div className="flex-1 space-y-2">
                        <div className="flex items-center gap-2">
                          <Badge variant={getClippingTypeBadgeVariant(clipping.type)}>
                            {getClippingTypeLabel(clipping.type)}
                          </Badge>
                          <span className="text-sm font-medium">{clipping.source}</span>
                        </div>
                        <CardTitle className="text-sm leading-tight">
                          {clipping.headline}
                        </CardTitle>
                        <CardDescription className="text-xs">
                          Inserido em: {new Intl.DateTimeFormat('pt-BR', {
                            day: '2-digit',
                            month: '2-digit',
                            year: 'numeric',
                            hour: '2-digit',
                            minute: '2-digit'
                          }).format(clipping.insertedAt)}
                        </CardDescription>
                      </div>
                    </div>
                  </CardHeader>
                </Card>
              ))
            )}
          </div>
        </div>

        {/* Submit Button */}
        <div className="flex justify-end space-x-2">
          <Button
            type="button"
            variant="outline"
            onClick={() => {
              setSelectedClippings([]);
              setRecipients('');
              setSubject('');
              setCustomMessage('');
              setError(null);
              setSuccess(null);
            }}
          >
            Limpar
          </Button>
          <Button type="submit" disabled={loading}>
            {loading ? 'Enviando...' : 'Enviar Newsletter'}
          </Button>
        </div>
      </form>
    </div>
  );
}
