// Get all clippings combined
export function getAllClippings(): Clipping[] {
  try {
    return [
      ...getPrintClippings(),
      ...getElectronicClippings(),
      ...getDigitalClippings()
    ].sort((a, b) => b.insertedAt.getTime() - a.insertedAt.getTime());
  } catch (error) {
    console.error('Error fetching all clippings:', error);
    return [];
  }
}

// Get clipping by ID
export function getClippingById(id: string): Clipping | undefined {
  try {
    return getAllClippings().find(clipping => clipping.id === id);
  } catch (error) {
    console.error('Error fetching clipping by ID:', error);
    return undefined;
  }
}

// Get clippings by type
export function getClippingsByType(type: 'print' | 'electronic' | 'digital'): Clipping[] {
  try {
    switch (type) {
      case 'print':
        return getPrintClippings();
      case 'electronic':
        return getElectronicClippings();
      case 'digital':
        return getDigitalClippings();
      default:
        return [];
    }
  } catch (error) {
    console.error(`Error fetching ${type} clippings:`, error);
    return [];
  }
}
